import PropTypes from 'prop-types';
import './Movie.css';

function Movie(props) {
	return (
		<div className="movies_movie">
			<div className="movies_img">
				<img class="movie_img" src={props.poster} alt={props.title} title={props.title} />
			</div>
			<div className="movies_data">
				<div className="movies_main">
					<h3 className="movie_title">{props.title}</h3>
					<h5 className="movie_year">{props.year}</h5>
					<ul className="genres">
						{props.genres.map((genre, index) => (
							<li key={index} className="genres_genre">
								{genre}
							</li>
						))}
					</ul>
				</div>
				<div className="movies_summary">
					<p className="movie_summary">{props.summary}</p>
				</div>
			</div>
		</div>
	);
}

Movie.propTypes = {
	id: PropTypes.number.isRequired,
	title: PropTypes.string.isRequired,
	summary: PropTypes.string.isRequired,
	poster: PropTypes.string.isRequired,
	year: PropTypes.number.isRequired,
	genres: PropTypes.arrayOf(PropTypes.string).isRequired
};

export default Movie;
